<template>
  <form>
    <textarea
      placeholder="Type a message and hit enter to send..."
      v-model="message"
      @keypress.enter.prevent="handleSubmit"
      ></textarea>
      <div class="error">
        {{  error }}
      </div>
  </form>
</template>

<script>
import { ref } from 'vue'
import getUser from '@/composables/getUser'
import useCollection from '@/composables/useCollection'
import { timestamp } from '@/firebase/config'

export default {
  setup() {
    const { user } = getUser();
    const { addDoc, error } = useCollection('messages-live-chat')

    const message = ref('');

    const handleSubmit = async () => {
      const chat = {
        name: user.value.displayName,
        message: message.value,
        createdAt: timestamp()
      }

      await addDoc(chat);

      if (!error.value) {
        message.value = '';
      }
    }

    return {message, handleSubmit, error}
  }
}

</script>

<style scoped>
form {
  margin: 10px;
}
textarea {
  background: black;
  background-size: cover;
  background-size: 100% 3px;
  width: 100%;
  max-width: 100%;
  margin-bottom: 6px;
  padding: 10px;
  box-sizing: border-box;
  border: 0;
  border-radius: 20px;
  font-family: inherit;
  outline: none;
  color: #00ff00;
  font-family: "Courier New", monospace;
  font-size: 18px;
  text-shadow: 0 0 10px #00ff00;
}
</style>